.container {
  display: inline-block;
  overflow: hidden;
  width: 100%;
  height: 42.5vw;
  margin-top: 6vw;
  @media screen and (max-width:600px) {
    margin-top: 13vw;
  }
  .backgroundImage {
    width: 100%;
    transition: 1s;
    display: block;
    &:hover {
      transform: scale(1.1);
    }
  }
}
